        p Si los beneficios son ms importantes que el precio, el consumidor pensar que el producto vale la pena, porque proyecta valor. Si sucede lo contrario, no comprar, porque no localiza valor en el producto. Por eso, la clave del xito de las empresas radica en solucionar un problema; los clientes lo comprenden de esta forma valorando el resultado que les suministra esa solucin y la eligen por encima de cualquier otra que tenga la competencia. 
    p.mb-5 Para establecer la mejor propuesta de valor, es importante identificar los factores que se incluyen para la consolidacin de esta; a continuacin, se detallan aspectos ms relevantes relacionados con este concepto. 
