      figcaption <b>Nota</b>. Tomada y adaptada de www.alfonsomorant.com /herramientas fundamentales para crear valor en la empresa modelo del lienzo de la propuesta de valor
    .row.bg11.justify-content-center.align-items-center.p-4.p-lg-5
