          img(src='@/assets/curso/tema2/img2.png', style="width: 350px").m-auto
      .col-lg-8
        p Como lo detallan Clark y Osterwalder (2017), un modelo de negocio representa las bases sobre las que una empresa crea, suministra y atrae valor. Los modelos de negocio cuentan con un Lienzo, que por lo general es creado por el modelo Canvas, el cual se distribuye en nueve mdulos bsicos que transmiten la lgica que persigue una marca para conseguir ingresos. Los modelos de negocio no son estticos, fijos o invariables; evolucionan con el paso del tiempo. Este Lienzo de modelos de negocio admite la identificacin e introduccin de innovaciones en el plan de negocio a lo largo del tiempo, contribuyendo claridad sobre la propuesta indiscutible de valor y la sostenibilidad de una marca.
        p Los siguientes son los cinco aprendizajes clave del lienzo del modelo de negocios.
    figure.mb-5
      img(src='@/assets/curso/tema2/img14.svg')
    p El modelo de negocio es el pedestal y la sntesis de toda estrategia que se aplique en las estructuras, procesos y sistemas de una marca (Clark y Osterwalder, 2017)
    Separador
    #t_2_2
    .titulo-segundo.color-acento-contenido
      h2 2.2 Modelo Canvas
    .row.align-items-center.mb-5
      .col-lg-4
        figure
          img(src='@/assets/curso/tema2/img3.png', style="width: 350px").m-auto
      .col-lg-8
        p Para Vaciero y Hernndez (2018) es una herramienta de gestin estratgica que permite conocer los aspectos clave de una marca: cmo se relacionan y ayudan entre s. Hace notoria la infraestructura, los clientes, la oferta y la situacin financiera de la empresa para el reconocimiento de las deficiencias y el anlisis del rendimiento.
        p Es un modelo visual con el que se puede organizar las ideas en el momento de crear un modelo de negocio; es ideal para establecer y disear modelos vanguardistas con el fin de crear valor para los clientes. Fue inventado por el consultor de negocios Alexander Osterwalder y el docente de sistemas de informacin y gestin Yves Pigneur. Su trabajo defini nueve categoras del modelo de negocio, las cuales encarnan los componentes bsicos de una marca.
        a.boton.color-acento-contenido.indicador__container(@click="modal1 = true")
          span Modelo Canvas
          .indicador--click(v-if="mostrarIndicador")
        ModalA(:abrir-modal.sync="modal1")
