                img(src='@/assets/curso/tema2/img4.png', style="width: 900px").m-auto
    .row.mb-5.justify-content-center
      .col-12.col-md-8
        .bg3.px-5.py-4.position-relative
          p.mb-3.ps-3.text-white El modelo Canvas sirve para la identificacin de los aspectos vitales de un modelo de negocio, mostrndolos de manera ordenada, de forma tal que se puedan encontrar las actividades que son ms importante, oportunidades de mejora, alternativas diferentes al modelo existente, entre otros. Es un modelo visual con el que se puede disear el lienzo de modelo de negocios con el que se pueden organizar mejor las ideas en el momento de crear un nuevo modelo de negocio. Es preciso para establecer y crear modelos vanguardistas con el fin de forjar valor para el pblico objetivo.
          img.img-a5.d-none.d-lg-block(src='@/assets/curso/tema2/img15.svg')
          img.img-a8(src='@/assets/curso/tema2/img16.svg')
    p.mb-5 Ms en detalle, el modelo Canvas sirve para:
    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-botones.p-4(titulo="Permitir identificar lo esencial")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Permitir identificar lo esencial
            p Se tiene una visin general de cul es de verdad el modelo de negocio. Muestra qu actividades no son tan importantes, e incluso cules se intercalan en el camino hacia los objetivos. Lleva la visin hacia lo principal y de esta forma se puede mejorar el modelo de negocio.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema2/img5.png', style="width: 350px").m-auto
      .tarjeta.color-acento-botones.p-4(titulo="Dar la base para la lluvia de ideas")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Dar la base para la lluvia de ideas
            p El modelo Canvas es el punto de partida ideal para permitir que las ideas y las del equipo nazcan libremente. Tambin se puede ensayar con modelos alternos que complementen la idea de negocio y encuentren nuevas opciones de promocin.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema2/img6.png', style="width: 350px").m-auto
      .tarjeta.color-acento-botones.p-4(titulo="Brindar una presentacin estructurada")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Brindar una presentacin estructurada
            p El modelo Canvas muestra en trminos generales y visuales el negocio, de una manera ms estructurada y clara. As, otros podrn comprender y discutir gilmente el modelo de negocio y todas las reas del proyecto recogern rpidamente la informacin que es importante para ellos y para la marca.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema2/img7.png', style="width: 350px").m-auto
      .tarjeta.color-acento-botones.p-4(titulo="Sentar la base de tus planes de negocios")
        .row
          .col-lg-8.mb-4.mb-lg-0
            h4 Sentar la base de tus planes de negocios
            p El modelo Canvas no reemplaza un plan de negocio detallado, pero con l se obtiene una organizacin que funciona como el esqueleto del plan de negocios.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema2/img8.png', style="width: 350px").m-auto
    .row.mb-3(data-aos="fade-right")
      .col-lg-8.bg4-2
        .p-2
          .row.d-flex.align-items-center
            .col-2
            .col-auto
              h3.ms-5 Las nueve categoras del lienzo de modelo de negocios del modelo Canvas
    p.mb-5 Ahora, se expone cada una de las nueve categoras que hacen parte del lienzo del modelo Canvas y las respectivas preguntas que pueden ayudar a darle forma a cada una.
    .row.justify-content-center.bg1.mb-5
      .col-lg-12
        figure.p-5
          .video
            iframe(width="560" height="315" src="https://www.youtube.com/embed/DQhdG8uRVCM" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
    .row.mb-3(data-aos="fade-right")
      .col-lg-8.bg4-2
        .p-2
          .row.d-flex.align-items-center
            .col-2
            .col-auto
              h3.ms-5 Pasos para la creacin de un modelo de negocio con el mtodo Canvas
    p.mb-5 Los siguientes son los pasos para crear un modelo de negocio con el modelo Canvas:
    .row.justify-content-center.mb-5
      .col-lg-10
        PasosA.color-acento-contenido.mb-5(tipo="n")
          .row.align-items-center
            .col-lg-8.mb-4.mb-lg-0
              h4 Aprender de la competencia
              p Se debe elegir varios competidores para analizar sus modelos de negocio. Con esta informacin se puede tener una visin real de lo que los clientes desean y lo que estn dispuestos a pagar. Generar preguntas como: Cules empresas son la competencia? Tienen presencia web? Cules son las ms populares? Cules son los ms parecidos a la propuesta de valor? Cul es el precio de sus productos? Cmo los fidelizan?
            .col-lg-4
              figure
                img(src='@/assets/curso/tema2/img9.png', style="width: 250px").m-auto
          .row.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img10.png', style="width: 250px").m-auto
            .col-lg-8
              h4.txt-a1 Hacer una lista de verificacin
              p.txt-a1 Se requiere reunir un equipo de 3 a 5 empleados, que tengan funciones estratgicas, experiencia o poder de anlisis. 
              p.txt-a1 Se debe aplicar el modelo Canvas y disponer de tableros, memos adhesivos, marcadores y/o computadores segn la metodologa que quieran aplicar para la graficacin del modelo.
          .row.align-items-center
            .col-lg-8.mb-4.mb-lg-0
              h4 Mapear y vincular los datos
              p Se debe hacer un rastreo completo del negocio, teniendo en cuenta los detalles ms importantes del modelo. Los conceptos deben ser claros y concretos. Al tener los elementos ubicados en el modelo Canvas, se sugiere descansar, para luego visualizar si se pudo haber perdido u olvidado de algo. Es importante que, si la marca tiene mltiples segmentos, se recomienda elegir un color para cada uno de estos. As, se identificar fcilmente la propuesta de valor y la fuente de ingresos de cada segmento.
            .col-lg-4
              figure
                img(src='@/assets/curso/tema2/img11.png', style="width: 250px").m-auto
          .row.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img12.png', style="width: 250px").m-auto
            .col-lg-8
              h4.txt-a1 Identificar el estado actual
              p.txt-a1 Lo que sucede hoy no se debe mezclar con ideas para el futuro. Tampoco se recomienda combinar varios departamentos en uno. En una empresa grande se pueden hallar diferentes propuestas de valor e incluso modelos de negocio; y se debe solicitar a todos los departamentos que diseen sus modelos de negocio para compararlos luego.
          .row.align-items-center
            .col-lg-8.mb-4.mb-lg-0
              h4 Revisar
              p Se debe repasar y asegurar que cada segmento est vinculado a una propuesta de valor y tambin a una fuente de ingresos. Se debe verificar que ambos lados del modelo Canvas se apoyen entre s. Conectando toda la informacin de forma coherente e integrada.
            .col-lg-4
              figure
                img(src='@/assets/curso/tema2/img13.png', style="width: 250px").m-auto
          .row.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img14.png', style="width: 250px").m-auto
            .col-lg-8
              h4.txt-a1 Clasificar
              p.txt-a1 Se recomienda clasificar el rendimiento del modelo de negocio entre 1 y 10 para cada una de los siguientes temas: Los cambios en los costos afectan a los clientes? El modelo previene su reaccin? El modelo de negocio se puede escalar? El modelo de negocio origina ingresos frecuentes? Antes de invertir, el negocio tiene ingresos lo suficientes? Lo que la empresa desarrolla se puede realizar por otros? Comparado con la competencia, el modelo de negocio est protegido?
    .row.align-items-center
      .col-lg-auto.p-4
        figure
          img(src='@/assets/curso/tema2/img18.svg', style="width: 90px").m-auto
      .col
        p.mb-0 Una vez terminados estos pasos, se debe #[b tomar una foto del Canvas para compartir y estudiar fcilmente la referencia], y generar una discusin sobre el modelo de negocio con otros integrantes de la empresa. Y, finalmente, motivar a los miembros del equipo a encontrar opciones de mejora (Vaciero y Hernndez, 2018).
    Separador
    #t_2_3
    .titulo-segundo.color-acento-contenido
      h2 2.3 Portafolio de negocio
    .row.align-items-center.mb-4
      .col-lg-4
        figure
          img(src='@/assets/curso/tema2/img15.png', style="width: 350px").m-auto
      .col-lg-8
        p Para Gonzlez (2017), una marca debe atender ciertos factores importantes que le permitan alcanzar los objetivos pactados. Cuando se conozca cmo crear el portafolio de negocios podr identificar, ordenar y atender de forma personalizada los factores que le permitan la toma de decisiones estratgicas y captar clientes fieles. El portafolio o cartera de negocio incluye un anlisis sobre los productos (bienes y servicios) y unidades comerciales de una empresa. Por lo tanto, el portafolio es la mezcla de bienes, servicios, marcas, inversiones y las unidades de negocio conformadas en la empresa.
        .row.bg12.align-items-center
          .col-lg.mb-4.mb-lg-0
            p.mb-0 En la gerencia, poder observar estas unidades de manera individual facilita tomar decisiones estratgicas para el resto del portafolio. De igual forma, cada unidad se puede entender como un activo. #[b El portafolio permite valorar la factibilidad de continuar, o no, con algn otro producto], as como proponer el monto de las inversiones.
          .col-lg-auto
            figure
              img(src='@/assets/curso/tema2/img19.svg', style="width: 80px").m-auto
    p.mb-5 La matriz BCG, fue creada en Estados Unidos por el Boston Consulting Group, tambin llamada Matriz de Portafolio Corporativo; se usa, generalmente, con la finalidad de perfeccionar la asignacin de recursos a todos los productos de la marca, hace una divisin de los productos en cuatro cuadrantes que se delimitan de acuerdo con el porcentaje de crecimiento y participacin respectiva en el mercado como se presenta a continuacin:
    .row.justify-content-center.mb-4
      .col-md-6.col-lg-4.mb-4.mb-lg-0
        .tarjeta.tarjeta-slide.izquierda.bg13(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              h4 Productos estrella
              p Aquellos que estn en crecimiento continuo y tiene una alta participacin en el mercado; requieren de una elevada inversin.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img20.svg')})`}")
      .col-md-6.col-lg-4
        .tarjeta.tarjeta-slide.derecha.bg14(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              h4 Productos Incgnita o interrogacin
              p Son productos nuevos, posiblemente innovadores, necesitan de una altsima inversin, teniendo un alto riesgo de fracaso, pero, de tener xito, se convierten en productos estrella y luego vacas lecheras.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img21.svg')})`}")
    .row.justify-content-center.mb-5
      .col-md-6.col-lg-4.mb-4.mb-lg-0
        .tarjeta.tarjeta-slide.izquierda.bg15(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              h4 Productos vaca lechera
              p Tienen alta participacin en el mercado, aunque su crecimiento al tope, porque llegaron a una fase de madurez donde permanecen, debido a ello son los productos qu ms recursos generan, porque no requieren de una alta inversin.
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img22.svg')})`}")
      .col-md-6.col-lg-4
        .tarjeta.tarjeta-slide.derecha.bg16(@mouseover="indicadorTarjetaSlide = false")
          .indicador--hover(v-if="indicadorTarjetaSlide")
          .tarjeta-slide__contenedor
            .tarjeta-slide__contenido.p-4.p-xl-5
              h4 Productos perro
              p Poca participacin en el mercado porque no tienen crecimiento. Usualmente son productos que llegaron al final del ciclo de vida. Se debe tomar la decisin de retirarlos del mercado o de inyectarles algn cambio estratgico (nuevo empaque, distintivo visual, sabor, olor, tecnologa, etc.).
            .tarjeta-slide__img(:style="{'background-image': `url(${require('@/assets/curso/tema2/img23.svg')})`}")
    .row.mb-3(data-aos="fade-right")
      .col-lg-6.bg4-1
        .p-2
          .row.d-flex.align-items-center
            .col-2
            .col-auto
              h3.ms-5 Importancia de contar con un portafolio de negocio
    .row.align-items-center.mb-5
      .col-lg-auto.p-4
        figure
