      .col
        p.mb-0 Las columnas de una marca son los productos y las unidades de negocio. Tener una gestin equilibrada de estos dos elementos lleva al xito y un portafolio de negocios trabaja como un grupo de activos que se necesitan para obtener una visin planeada. Otros de los propsitos que cumple un portafolio son:
    .row.justify-content-center.mb-5
      .col-10.col-lg.my-2.my-lg-0.m-0.m-lg-2
        .row.h-100.bg17
