    .row.py-0.py-lg-5.mb-5.justify-content-center
      .col-12.col-md-8
        .bg3.px-5.py-4.position-relative
          p.my-2.ps-3.text-white No confundir un portafolio de productos con uno de negocio. El portafolio de producto es el grupo de todos los bienes y servicios que la marca ofrece al mercado, mientras que el de negocio abarca tambin inversiones, equipos, maquinaria, activos fijos y comerciales.
          img.img-a9.d-none.d-lg-block(src='@/assets/curso/tema2/img25.svg')
          img.img-a10(src='@/assets/curso/tema2/img26.svg')
