            .col-2
            .col-auto
              h3.ms-5 Elaboracin de un portafolio de negocio
    .row.align-items-center.mb-5
      .col-lg-auto.p-4
        figure
          img(src='@/assets/curso/tema2/img27.svg', style="width: 90px").m-auto
      .col
        p.mb-0 Para la elaboracin de un portafolio de negocios que fomente las bases de una empresa, se requiere realizar una investigacin y una planeacin detallada sobre la organizacin, los servicios a ofertar y dems elementos para comercializar, los siguientes son los elementos necesarios para lograrlo:
    .tarjeta--azul1.p-4.p-lg-5
      SlyderA(tipo="b")(data-aos="fade-right")
        .row.align-items-center.justify-content-center
          .col-lg-7.mb-4.mb-lg-0
            h4 1. Identificacin de unidades estratgicas de negocio
            p Para iniciar con la planificacin de un portafolio se requiere identificar varias Unidades Estratgicas de Negocio (UEN). Las siguientes son las principales caractersticas de las unidades de negocio:
            ul.lista-ul
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Nivel de independencia y autonoma.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Tipo de competidores.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Planeacin estratgica.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Rentabilidad.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Segmentos de mercado.
          .col-lg-5
            figure
              img.img-t(src='@/assets/curso/tema2/img20.png', style="width: 450px").m-auto
        .row.align-items-center.justify-content-center
          .col-lg-7.mb-4.mb-lg-0
            h4 2. Conocimiento del mercado
            p El conocimiento del nicho de mercado facilita la planificacin del portafolio de negocio, porque permite hallar opciones rentables y de mejor crecimiento. 
            p Estos son algunos elementos que se deben tener en cuenta al examinar el mercado:
            ul.lista-ul
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Tasa de crecimiento
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Tendencias de precios. 
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Tamao. 
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Competencia. 
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Rentabilidad. 
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Retorno de inversin. 
          .col-lg-5
            figure
              img.img-t(src='@/assets/curso/tema2/img58.png', style="width: 450px").m-auto      
        .row.align-items-center.justify-content-center
          .col-lg-7.mb-4.mb-lg-0
            h4 3. Evaluacin de la fuerza competitiva de la unidad de negocio en el mercado objetivo
            p Toda unidad de negocio posee su propia competencia, por lo que se deben analizar independientemente. Entender quin es la competencia (fortalezas y debilidades) puede permitir a la marca instaurar objetivos comerciales. Al hacer una evaluacin sobre la fuerza competitiva se pueden identificar:
            ul.lista-ul
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Elementos diferenciales de la competencia.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Recursos con los que cuenta la competencia.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Cmo usar los activos para ser ms visibles.
              li 
                i.fas.fa-check-circle(style="color:#764CF0")
                p.mb-0 Cules son las debilidades de la marca y qu requiere para erradicarlos.
          .col-lg-5
            figure
              img.img-t(src='@/assets/curso/tema2/img21.png', style="width: 450px").m-auto
    Separador
    #t_2_4
    .titulo-segundo.color-acento-contenido
      h2 2.4 Ciclo de vida del producto digital
    .row.align-items-center.mb-5
      .col-lg-4
        figure
          img(src='@/assets/curso/tema2/img22.png', style="width: 350px").m-auto
      .col-lg-8
        p El ciclo de vida de un producto es el proceso por el que pasa un producto durante su existencia. Generalmente est dividido en cuatro etapas: introduccin, crecimiento, madurez y declive, etapas que est directamente ligadas con la matriz BCG (matriz de portafolio corporativo). Para los productos digitales, el ciclo de vida es exactamente la misma situacin. Los especialistas en mercadeo usan los conocimientos sobre el ciclo de vida de un producto para la toma de decisiones y desarrollo de estrategias significativas para mantenerse en el mercado.
        p El siguiente video detalla cada una de las fases del ciclo de vida de un producto digital y los elementos que las diferencian.
    .row.justify-content-center.bg1.mb-5
      .col-lg-12
        figure.p-5
          .video
            iframe(width="560" height="315" src="https://www.youtube.com/embed/Zt6hQyGX554" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)
            
    .row.mb-3(data-aos="fade-right")
      .col-lg-7.bg4-2
        .p-2
          .row.d-flex.align-items-center
            .col-2
            .col-auto
              h3.ms-5 Cmo funciona el ciclo de vida de los productos digitales?
    .row.align-items-center.mb-3
      .col-lg-8.mb-4.mb-lg-0
        .bg19
          .row
            .col-12
              p Es vital conocer cmo es el ciclo de vida de un producto digital y saber sus especificaciones de acuerdo con las diferencias que pueda tener con el producto tradicional. La transformacin digital exige ms a los empresarios, bsicamente a quienes apuestan e invierten en productos para el mundo digital.
              .row.align-items-center.mb-3
                .col-lg-auto
                  figure
                    img(src='@/assets/curso/tema2/img28.svg', style="width: 140px").m-auto
                .col
                  p La introduccin de productos digitales, por ejemplo, puede verse favorecida por algunas herramientas, como por ejemplo redes sociales, sitio web, blogs, foros, chats, etc., que de manera econmica, creativa y recursiva ayudan a las Pymes para lograr posicionar no solo la marca, sino los productos. Adems, se entiende que el uso de estrategias acciones de mercadeo digital ayudan a que el producto sea exitoso en poco tiempo.
              p En la etapa de introduccin, se debe haber desarrollado el mapeo del <em>buyer</em> persona de la marca. Esa informacin ayuda a comprender la necesidad real del cliente, mejorando la precisin de las acciones estratgicas, as como la optimizacin en el alcance territorial del producto.
      .col-lg-4
        figure
          img(src='@/assets/curso/tema2/img23.png', style="width: 450px").m-auto
    p.mb-5 Por su lado, en las etapas de crecimiento y madurez, un elemento para tener en cuenta es la analtica (mtricas). El uso de los indicadores correctos ser de gran utilidad para el producto digital. A continuacin, algunas mtricas importantes:
    .row.justify-content-center.mb-5
      .col-md-8.col-lg-5.col-xl-3.col-xxl.m-1
        .row.align-items-center.h-100.bg21
          .col-3.col-lg-auto.h-100.bg20
            figure.p-2
              img(src='@/assets/curso/tema2/img29.svg', alt='Texto que describa la imagen')
          .col.align-items-center
            p.mb-0 Costo de adquisicin por cliente.
      .col-md-8.col-lg-5.col-xl-3.col-xxl.m-1
        .row.align-items-center.h-100.bg21
          .col-3.col-lg-auto.h-100.bg20
            figure.p-2
              img(src='@/assets/curso/tema2/img30.svg', alt='Texto que describa la imagen')
          .col.align-items-center
            p.mb-0 Tasa de cancelacin o abandono (#[i churn]).
      .col-md-8.col-lg-5.col-xl-3.col-xxl.m-1
        .row.align-items-center.h-100.bg21
          .col-3.col-lg-auto.h-100.bg20
            figure.p-2
              img(src='@/assets/curso/tema2/img31.svg', alt='Texto que describa la imagen')
          .col.align-items-center
            p.mb-0 #[i Ticket] promedio.
      .col-md-8.col-lg-5.col-xl-3.col-xxl.m-1
        .row.align-items-center.h-100.bg21
          .col-3.col-lg-auto.h-100.bg20
            figure.p-2
              img(src='@/assets/curso/tema2/img32.svg', alt='Texto que describa la imagen')
          .col.align-items-center
            p.mb-0 #[i Lifetime value].
      .col-md-8.col-lg-5.col-xl-3.col-xxl.m-1
        .row.align-items-center.h-100.bg21
          .col-3.col-lg-auto.h-100.bg20
            figure.p-2
              img(src='@/assets/curso/tema2/img33.svg', alt='Texto que describa la imagen')
          .col.align-items-center
            p.mb-0 Margen de contribucin, entre otras.
    p Con estas mtricas se pueden identificar lo bueno y lo malo en la actuacin y alineacin del producto digital con relacin a las solicitudes del cliente, de tal forma que se puedan usar estos datos para realizar las mejoras correspondientes (Gonzlez, 2017).
