    #t_2_5
    .titulo-segundo.color-acento-contenido
      h2 2.5 La marca
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema2/img24.png', style="width: 450px").m-auto
      .col-lg-8
        p Como lo expresan Kotler y Keller (2012), la marca es uno de los factores estratgicos vitales de una compaa porque diariamente adquiere ms protagonismo. Actualmente se vive una etapa bajo el prisma del mercadeo de percepciones y, por tanto, sobresale la marca frente al producto. 
        p Para lograr el posicionamiento en la mente del consumidor, la marca debe tener el mejor y mayor reconocimiento en el mercado donde haga presencia. Los productos son cada vez ms parecidos por lo que se le hace difcil al consumidor distinguir los atributos de uno y de otro producto.
        p Adems de ser el principal identificador del producto, la marca es una garanta ubicndola en un plano superior. La marca le entrega una identidad y forja una relacin emocional con los clientes.
    .row.mb-3(data-aos="fade-right")
      .col-lg-3.bg4
        .p-2
          .row.align-items-center
            .col-2
            .col-auto
              h3 Funciones de la marca
    p.mb-3.mb-lg-5 Las funciones de la marca son:
    .row.bg22.align-items-center.mb-5
      .col-lg-7.mb-4.mb-lg-0
        ul.lista-ul
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Garantizar que los clientes reconozcan sus productos.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Permitir a los negocios diferenciar sus productos.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Proyectar la imagen y reputacin de la empresa.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Tener licencias y suministrar fuentes directas de ingresos a travs de regalas.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Encarnar un factor concluyente en los acuerdos de franquicia.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Ser un significativo activo comercial.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Incitar a los negocios a invertir en el sostenimiento o mejora de la calidad del producto.
          li 
            i.fas.fa-check(style="color:#764CF0")
            p.mb-0 Ser de utilidad para lograr financiacin.
      .col-lg-5
        figure
          img(src='@/assets/curso/tema2/img34.svg', style="width: 350px").m-auto
    .row.mb-3(data-aos="fade-right")
      .col-lg-3.bg4
        .p-2
          .row.align-items-center
            .col-2
            .col-auto
              h3 Tipos de marca
    p.mb-5 Kotler y Keller (2012), tambin hacen la siguiente clasificacin de marcas segn la relacin que tengan con la organizacin que las sostiene:
    TabsB.color-acento-contenido.mb-5(data-aos="fade") 
      .py-4.py-md-5(titulo="Marca nica" :icono="require('@/assets/curso/tema2/img35.svg')")
        .row.align-items-center.p-4
          .col-lg-8.mb-4.mb-lg-0
            h4 Marca nica
            p Es una estrategia ventajosa, porque los clientes, precio, canales de distribucin y posicionamiento de marca no cambian mucho entre categoras de producto. Facilita el lanzamiento de productos nuevos. Disminuye los costos de distribucin. Es as como todos los productos se identifican con la empresa alcanzando una imagen corporativa compacta.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2/img25.png", style="width: 350px")
      .py-4.py-md-5(titulo="Marca individual" :icono="require('@/assets/curso/tema2/img35.svg')")
        .row.align-items-center.p-4
          .col-lg-8.mb-4.mb-lg-0
            h4 Marca individual
            p Estrategia que consiste en otorgar un nombre a cada producto, o a cada gama de productos. En este caso se hace difcil asociar el negocio con cada uno de sus productos y es complejo lograr una imagen integral de la empresa. 
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2/img26.png", style="width: 350px")
      .py-4.py-md-5(titulo="Marca mixta" :icono="require('@/assets/curso/tema2/img35.svg')")
        .row.align-items-center.p-4
          .col-lg-8.mb-4.mb-lg-0
            h4 Marca mixta
            p Se piensa que el futuro ser la combinacin de marca nica y marca individual. O sea, tal como una persona se identifica a travs de nombre y apellido, una marca puede hacerlo con sus productos, por ejemplo, Mazda 2, Mazda 3, Mazda 6, Mazda CX-5, Mazda CX-30, Mazda BT-50. 
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2/img27.png", style="width: 350px")
      .py-4.py-md-5(titulo="Marca de la distribucin" :icono="require('@/assets/curso/tema2/img35.svg')")
        .row.align-items-center.p-4
          .col-lg-8.mb-4.mb-lg-0
            h4 Marca de la distribucin
            p Actualmente, es un nuevo elemento dentro del concepto de marca. Tambin es conocida como marcas blancas, y se refiere a las marcas que son creadas, promocionadas y distribuidas por el mismo fabricante, donde se encuentran con mayor frecuencia en supermercados, hipermercados, almacenes de cadena o establecimientos comerciales que ponen su nombre a sus propios productos y los venden a precios competitivos.
          .col-lg-4
            figure
              img(src="@/assets/curso/tema2/img28.png", style="width: 350px")
    p.mb-5 Adems de las diferentes caractersticas propias de la clasificacin de las marcas, tambin existen marcas que se ajustan segn el rol que desarrollan o la relacin que tienen con otras marcas; en el siguiente recurso se destacan elementos propios de este tipo de marcas:
    .row.justify-content-center.align-items-center(data-aos="fade-left").mb-5
      .col-lg-12.col-md-6
        SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
          
          .tarjeta.bg2.p-4.h-100.position-relative.slyderF-media
            figure.mb-3
              img.img-t(src='@/assets/curso/tema2/img38.svg').mx-auto
            h4.text-center Marca madre o marca paraguas
            p.text-center Componen el punto de referencia de la oferta, abrigando al producto y representan familias que ya poseen una identidad especfica.
            img.img-a(src='@/assets/componentes/circulo.svg')
            img.img-a2(src='@/assets/componentes/rectangulo.svg')
          .tarjeta.bg2.p-4.h-100.position-relative.slyderF-media
            figure.mb-3
              img.img-t(src='@/assets/curso/tema2/img39.svg').mx-auto
            h4.text-center Submarca
            p.text-center Delimita una oferta diferente modificando la asociacin de la marca madre en contextos especficos.
            img.img-a(src='@/assets/componentes/circulo.svg')
            img.img-a2(src='@/assets/componentes/rectangulo.svg')
          .tarjeta.bg2.p-4.h-100.position-relative.slyderF-media
            figure.mb-3
              img.img-t(src='@/assets/curso/tema2/img40.svg').mx-auto
            h4.text-center Marca respaldadora
            p.text-center Marca establecida, con prestigio y reconocimiento que otorga credibilidad y confianza a sus productos.
            img.img-a(src='@/assets/componentes/circulo.svg')
            img.img-a2(src='@/assets/componentes/rectangulo.svg')
          .tarjeta.bg2.p-4.h-100.position-relative.slyderF-media
            figure.mb-3
              img.img-t(src='@/assets/curso/tema2/img36.svg').mx-auto
            h4.text-center Marca respaldada
            p.text-center Marca establecida que respalda, ampara y cubre a submarcas y/o productos.
            img.img-a(src='@/assets/componentes/circulo.svg')
            img.img-a2(src='@/assets/componentes/rectangulo.svg')
          .tarjeta.bg2.p-4.h-100.position-relative.slyderF-media
            figure.mb-3
              img.img-t(src='@/assets/curso/tema2/img37.svg').mx-auto
            h4.text-center Marca de beneficio
            p.text-center Marcas que incluyen un beneficio determinado al producto a travs de un atributo que entrega identidad, ventaja competitiva y diferenciacin, concediendo el liderazgo en el mercado.
            img.img-a(src='@/assets/componentes/circulo.svg')
            img.img-a2(src='@/assets/componentes/rectangulo.svg')
    p.mb-5 La clasificacin de las marcas incluye entonces la relacin con la organizacin a la que representa, el rol que desarrolla como parte de su dinmica, pero tambin la gama en la que puede ser clasificada, a continuacin, se detallan cuatro de las principales gamas de marcas as:
    .row.justify-content-center.mb-5
