              h3 Identidad de marca
    p.mb-5 Es el conjunto de activos y pasivos afines al nombre y smbolo de la marca que incluyen o sustraen el valor suministrado por un producto a la empresa y/o a sus clientes. Algunas de las categoras de activos son:
